.el-range-editor .el-range-input {
  background: #0B5CB9;
}
.el-date-editor,
.el-range-input {
  color: #fff !important;
}
.el-date-editor .el-range__icon {
  line-height: 25px;
}
.el-input__icon .el-range__close-icon {
  line-height: 25px;
}
.el-date-editor .el-range-separator {
  line-height: 25px;
  color: #fff;
}
.el-select .el-input .el-input__suffix .el-icon-arrow-up {
  -ms-transform: rotate(180deg) \0;
}
.el-select,
.el-input {
  width: 108px !important;
  height: 30px;
}
.el-date-editor {
  width: 265px !important;
}
.el-input__inner {
  height: 30px;
  background-color: #0B5CB9 !important;
  border: none;
  border-radius: 20px;
  font-size: 10px;
  color: #fff;
}
.demonstration,
.el-date-editor--datetimerange {
  display: inline-block !important;
  vertical-align: middle;
  line-height: 24px;
}
.el-input__icon {
  line-height: 30px;
}
.el-table th,
.el-table tr {
  background: transparent;
}
.el-table,
.el-table__expanded-cell {
  background: transparent;
}
.el-table td,
.el-table th.is-leaf {
  border: none;
}
.el-table::before {
  height: 0px;
}
.el-table tbody tr:hover > td {
  background-color: transparent !important;
}
.el-table__body-wrapper .el-table__row td:nth-child(1),
.el-table__body-wrapper .el-table__row td:nth-child(2),
.el-table__body-wrapper .el-table__row td:nth-child(3),
.el-table__body-wrapper .el-table__row td:nth-child(4) {
  color: #8BAFBA;
}
.el-table__body-wrapper .el-table__row td:nth-child(5) {
  color: #E85259;
}
.el-table__body-wrapper .el-table__row td:nth-child(6) {
  color: #6794DD;
}
.el-table__body-wrapper .el-table__row td:nth-child(7),
.el-table__body-wrapper .el-table__row td:nth-child(8),
.el-table__body-wrapper .el-table__row td:nth-child(9) {
  color: #fff;
}
.el-table__body-wrapper .el-table__row td:nth-child(10) {
  color: #F4B890;
}
.el-pagination.is-background .btn-next,
.el-pagination.is-background .btn-prev,
.el-pagination.is-background .el-pager li {
  background: transparent;
  border: 1px solid #fff;
  color: #93D7F5;
  font-weight: 400;
}
.el-pagination.is-background .el-pager li.active {
  border-color: transparent;
}
.page-btn {
  position: absolute;
  bottom: 50px;
  right: 30px;
}
